@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@700&family=Poppins:wght@400;700&family=Oswald:wght@700&family=Roboto:wght@400&family=Lato:wght@400&family=Open+Sans:wght@400&display=swap);
@tailwind base;
@tailwind components;
@tailwind utilities;

/* index.css */

:root {
  --royal-purple: #822BD9;
  --deep-purple: #7326BF;
  --lavender: #C9ACF2;
  --electric-blue: #3212A6;
  --light-grey: #F2F2F2;
  --mint-green: #3EEDC5;
}

body {
  font-family: 'Roboto', sans-serif;
}

h1, h2, h3, h4 {
  font-family: 'Montserrat', sans-serif;
}

@tailwind base;
@tailwind components;
@tailwind utilities;


.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

